import { reduxForm } from 'redux-form'
import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import { t } from 'i18next'
import { pathOr } from 'ramda'
import FinishResetPasswordForm from 'Shared/forms/ResetPassword/FinishResetPasswordForm'
import validate from 'Shared/validators/resetPassword'
import AuthService from 'Shared/services/AuthService'
import NotificationService from 'Shared/services/NotificationService'

const authService = new AuthService()

export const formName = 'EmployerFinishResetPassword'

let Form = reduxForm({
  form: formName,
  validate
})(FinishResetPasswordForm)

const mapDispatchToProps = (dispatch, ownProps) => {
  const redirectPath = pathOr('/employer/login', ['location', 'state', 'from'], ownProps)

  return {
    resetPassword: async (values) => {
      try {
        await authService
          .finishResetPasswordEmployer(
            values.newPassword,
            ownProps.match.params.token
          )
        NotificationService.success(t('alerts.resetPasswordSuccess'))
        return ownProps.history.push(redirectPath)
      } catch (err) {
        NotificationService.error(t('alerts.resetPasswordFailed'))
        return console.error(err)
      }
    }
  }
}

export default withRouter(connect(null, mapDispatchToProps)(Form))



